import debounce from 'lodash/debounce';
import React, { useCallback, useMemo, useState, forwardRef, Ref } from 'react';
import { useCombinedRefs } from 'hooks/useCombinedRefs';

export interface Props {
  onImpressionStart?: () => void;
  onImpressionEnd?: () => void;
  rootMargin?: string;
  /**
   * ëª‡ í¼ì„¼íŠ¸ ì´ìƒ í™”ë©´ì— í‘œì‹œë˜ë©´ "ë³´ì¸ë‹¤"ê³  ìƒê°í• ì§€ ê²°ì •í•©ë‹ˆë‹¤.
   * ì˜ˆë¥¼ ë“¤ì–´, 0.5ê°€ ì£¼ì–´ì§€ë©´ 0.5 ì´ìƒì´ ë³´ì—¬ì¡Œì„ ë•Œ `onImpressionStart`, ì´í•˜ê°€ ë³´ì—¬ì¡Œì„ ë•Œ `onImpressionEnd`ê°€ í˜¸ì¶œë©ë‹ˆë‹¤.
   * @default 0
   */
  areaThreshold?: number;
  /**
   * ëª‡ ë°€ë¦¬ì„¸ì»¨ë“œ ì´ìƒ í™”ë©´ì— í‘œì‹œë˜ê±°ë‚˜ ì‚¬ë¼ì ¸ì•¼ ì‹¤ì œë¡œ ê°’ì„ ì—…ë°ì´íŠ¸í• ì§€ ê²°ì •í•©ë‹ˆë‹¤.
   * ì˜ˆë¥¼ ë“¤ì–´, ê°’ì´ 2000ì´ë©´ 2ì´ˆ ì‚¬ì´ì— ìš”ì†Œì˜ í‘œì‹œ ìƒíƒœê°€ ë°”ë€Œì–´ë„ ì²« ìƒíƒœì™€ ë ìƒíƒœê°€ ê°™ë‹¤ë©´ ì´ë²¤íŠ¸ê°€ í˜¸ì¶œë˜ì§€ ì•ŠìŠµë‹ˆë‹¤.
   * @default 0
   */
  timeThreshold?: number;
  className?: string;
  children?: React.ReactNode;
}

const ImpressionArea = forwardRef(
  (
    { rootMargin, areaThreshold, timeThreshold, className, children, onImpressionStart, onImpressionEnd }: Props,
    forwardedRef: Ref<HTMLDivElement>
  ) => {
    const impressionRef = useImpressionRef<HTMLDivElement>({
      onImpressionStart,
      onImpressionEnd,
      areaThreshold,
      timeThreshold,
      rootMargin,
    });

    const ref = useCombinedRefs(forwardedRef, impressionRef);

    return (
      <div className={className} ref={ref}>
        {children}
      </div>
    );
  }
);

export default ImpressionArea;

export function useImpressionRef<Element extends HTMLElement>({
  onImpressionStart,
  onImpressionEnd,
  timeThreshold = 0,
  rootMargin = '-8px',
  areaThreshold: impressionRatio = 0,
}: Pick<Props, 'rootMargin' | 'onImpressionStart' | 'onImpressionEnd' | 'timeThreshold' | 'areaThreshold'>) {
  const [isImpressed, setIsImpressed] = useState(false);

  const handleImpressionStart = useDebouncedCallback(
    () => {
      if (!isImpressed) {
        if (onImpressionStart !== undefined) {
          onImpressionStart();
        }
        setIsImpressed(true);
      }
    },
    timeThreshold,
    [isImpressed, setIsImpressed, onImpressionStart]
  );

  const handleImpressionEnd = useDebouncedCallback(
    () => {
      if (isImpressed) {
        if (onImpressionEnd !== undefined) {
          onImpressionEnd();
        }
        setIsImpressed(false);
      }
    },
    timeThreshold,
    [isImpressed, setIsImpressed, onImpressionEnd]
  );

  const handleImpression = useCallback(
    (entries: IntersectionObserverEntry[]) => {
      const entry = entries[0];
      const currentRatio = entry.intersectionRatio;

      const didImpressionEnd = impressionRatio === 0 ? !entry.isIntersecting : currentRatio < impressionRatio;

      if (didImpressionEnd) {
        handleImpressionStart.cancel();
        handleImpressionEnd();
      } else {
        handleImpressionEnd.cancel();
        handleImpressionStart();
      }
    },
    [handleImpressionEnd, handleImpressionStart, impressionRatio]
  );

  const observer = useMemo(() => {
    if (typeof IntersectionObserver === 'undefined') {
      return undefined;
    }

    return new IntersectionObserver(handleImpression, {
      rootMargin,
      threshold: impressionRatio,
    });
  }, [handleImpression, impressionRatio, rootMargin]);

  const ref = useCallback(
    (element: Element | null) => {
      if (observer === undefined) {
        return;
      }

      observer.disconnect();
      // unmountì‹œ ref callbackì—ëŠ” nullì´ ë„˜ì–´ì˜´
      if (element !== null) {
        observer.observe(element);
      }
    },
    [observer]
  );

  return ref;
}

function useDebouncedCallback<Func extends (...args: any[]) => any>(func: Func, delay: number, deps: any[] = []) {
  // eslint-disable-next-line react-hooks/exhaustive-deps
  return useMemo(() => debounce(func, delay), [delay, ...deps]);
}
